body {
    margin: 0;
    padding: 15px 0;
    color: #222;
    background: #f4f4f4;
    font: 400 16px CoreSans, Arial,sans-serif;
}

:root {
    --default-color: #222;
    --primary-color: tomato;
    --secondary-color: dodgerblue;
    --accent-color: mediumslateblue;
}

a { 
    color: var(--default-color);
    /* color: var(--secondary-color); */
}
a:hover { 
    text-decoration: underline;
}
*:focus { 
    outline: none;
}

ul, li { 
    list-style: none; 
    padding: 0; 
    margin: 0;
}
  
input {
    padding: 10px;
    border-radius: 4px; 
    outline: none; 
    margin-right: 10px; 
    border: 1px solid #ddd;
}
  
button {
    padding: 15px; 
    border-radius: 4px;
    color: var(--color-accent);
    cursor: pointer;
    border: 1px solid var(--color-accent);
}
button:hover { 
    border-color: var(--color-accent); 
}

.button-search {
    border-color: var(--primary-color); 
    background: var(--primary-color); 
    color: #fff;
    min-width: 100px;
    font-weight: bold;
}
.button-search:hover {
    -webkit-box-shadow: 0 0 1px 1px #ccc;
            box-shadow: 0 0 1px 1px #ccc;
}

.interactions { 
    text-align: center;
}
.interactions .search {
    margin-bottom: 15px;;
}
  
.page {
    margin: 30px;
}

.search {
    font-size: 16px;
}
.search input {
    min-width: 280px;
    padding: 15px;
}
  
.posts-list {
    width: 90%;
    background: #fff;
    margin: 30px auto;
    border: 1px solid #e3e3e3;
    border-radius: 4px;
    display: -ms-flexbox;
    display: flex;
    line-height: 24px;
    font-size: 16px;
    padding: 0 10px; 
    -ms-flex-pack: justify; 
        justify-content: space-between;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.posts button {
    margin: 0 5px 0 0;
    background: transparent;
    color: inherit;
    text-align: inherit; 
    -webkit-font-smoothing: inherit; 
    padding: 5px;
    font-size: inherit;
    cursor: pointer;
}
.posts button.active {
    border-radius: 0;
    border-bottom: 1px solid #38BB6C;
}  
.posts {
    width: 32%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    line-height: 24px;
    white-space: nowrap; 
    margin: 10px;
    padding: 10px 15px; 
}
.posts > span { 
    overflow: hidden; 
    -o-text-overflow: ellipsis; 
       text-overflow: ellipsis; 
    display: inline-block;
    vertical-align: middle;
}
.posts .title {
    font-size: 15px;
    margin-bottom: 15px;
    border-bottom: 2px solid var(--primary-color);
    position: relative;
}
.posts .title-link {
    width: 80%;
    padding: 0 40px 5px 0; 
    display: inline-block;
    vertical-align: middle;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap; 
    color: var(--primary-color);
    text-decoration: none;
}
.posts .title-link:hover {
    color: var(--default-color);
}
.posts .points {
    position: absolute;
    right: 0;
    border-radius: 50%;
    border: 2px solid var(--primary-color);
    background: #fff;
    text-align: center;
    padding: 10px 2px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-size: 14px;
}
.posts .points .button-sort {
    margin-right: 1px;
}
.posts .points-wrap {
    display: block;
}
.posts .button-cancel {
    background: #ccc;
}
.posts .button-cancel:hover {
    -webkit-box-shadow: inset 0 0 1px 1px rgba(27,31,35,0.2);
            box-shadow: inset 0 0 1px 1px rgba(27,31,35,0.2);
}

.error-message {
    color: var(--primary-color);
    text-align: center;
    margin: 0 auto 15px;
    width: 80%;
}

.empty { 
    margin: 200px; 
    text-align: center; 
    font-size: 16px;
}

.loading-wrap {
    text-align: center;
    margin: 0 auto 15px;
}
.loading .fa-spinner {
    width: 2em;
    height: 2em;
    color: var(--primary-color);
    display: inline-block;
    vertical-align: middle;
}
.loading .text {
    color: var(--primary-color);
    display: inline-block;
    vertical-align: middle;
    margin: 0 0 0 5px;
}

.button-active {
    color: dodgerblue !important;
}

.message-time {
    text-align: center;
    margin: 0 0 30px 0;
}
.message-time .title {
    display: inline-block;
    vertical-align: middle;
    margin: 0 5px 0 0;
    font-size: 16px;
}
.fa-coffee {
    font-size: 30px;
    width: 2em;
    height: inherit;
    display: inline-block;
    vertical-align: middle;
    color: brown;
}


